<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <img class = 'background' src= '@/assets/mission/bg_5.png'>
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <button class="info" @click="openinfo">
                <v-icon size="25">mdi-information-variant-circle</v-icon>
            </button>
            <div class="content-container" justify="center" align="center">
                <div class="correctSound mb-4">
                    <p> </p>
                    <!-- <audio controls src="@/assets/.wav"></audio> -->
                    <v-btn @click="toggleAudio(sounds[3])" fab>
                        <v-icon large  :style="{ color: sounds[3].isActive ? 'green' : 'black' }">mdi mdi-volume-high</v-icon>
                    </v-btn>
                    
                </div>
                <br>
                <p>     ?</p>
                <div class="soundDiv">
                    <v-row class="sound-item">
                        <v-col cols="4">
                            <v-btn @click="toggleAudio(sounds[0])" fab>
                                <v-icon large  :style="{ color: sounds[0].isActive ? 'green' : 'black' }">mdi mdi-volume-high</v-icon>
                            </v-btn>
                        </v-col>
                        <v-col cols="4">
                            <v-btn @click="toggleAudio(sounds[1])" fab>
                                <v-icon large  :style="{ color: sounds[1].isActive ? 'green' : 'black' }">mdi mdi-volume-high</v-icon>
                            </v-btn>
                        </v-col>
                        <v-col cols="4">
                            <v-btn @click="toggleAudio(sounds[2])" fab>
                                <v-icon large  :style="{ color: sounds[2].isActive ? 'green' : 'black' }">mdi mdi-volume-high</v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>
                    <v-row class="sound-item">
                        <v-col cols="auto">
                            <v-checkbox hide-details class="shrink ml-4 mr-1" style="transform: scale(1.5);" v-model="userResult" :value="sounds[0].value"></v-checkbox>
                        </v-col>
                        <v-col cols="auto">
                            <v-checkbox hide-details class="shrink ml-4 mr-1" style="transform: scale(1.5);" v-model="userResult" :value="sounds[1].value" color="success"></v-checkbox>
                        </v-col>
                        <v-col cols="auto">
                            <v-checkbox hide-details class="shrink ml-4 mr-1" style="transform: scale(1.5);" v-model="userResult" :value="sounds[2].value"></v-checkbox>
                        </v-col>
                    </v-row>
                </div>
                <br>
                <v-btn class="custom-submit-button mt-3 pl-7 pr-7" color="#EF8200" @click="submitSoundRes"></v-btn>
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card>
                <v-card-text>
                    <v-row align="center" justify="center">
                        <v-col align="center">
                            <div style="height:70%; width:70%; display: flex; align-items: center; justify-items: center;">
                                <v-img v-if="userResult === ''" src="@/assets/O.png" style="max-height:100%; max-width:100%;"></v-img>
                                <v-img v-else src="@/assets/X.png" style="max-height:100%; max-width:100%;"></v-img>
                            </div>
                            <br>
                            <div v-if="userResult === ''" class="text-center">
                                <h3>!</h3> 
                                <h4>  .</h4>
                            </div>
                            <div v-else class="text-center">
                                <h3>  ?</h3>
                                <br>
                                <p>    </p>
                                <p> !</p>
                                <p>   </p>
                            </div>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-card-actions class="card">
                <v-btn @click="handleDialogConfirmation(userResult === '')" class="ok-btn mt-3 pl-10 pr-10" color="#EF8200"></v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="infodialog" class="infodialog">
            <v-card-text class="title">
                <h3></h3>
                <img @click="infodialog = false" src="@/assets/mission/close.png" class="closeinfo">
            </v-card-text>
                <img src="@/assets/mission/missionInfo/info5.jpeg" class="pic">
        </v-dialog>

    </div>
</template>

<script>
import {Howl} from 'howler';

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result5: {
            type: Boolean,
        }
    },
    data(){
      return {
          userResult: '', //   
          showPopup: false, // 
          dialog: false,
          infodialog: false,

          sounds: [
                { id: 1, src: "/watertoad/.mp3", isActive: false, howl: null, value: '' },
                { id: 2, src: "/watertoad/.mp3", isActive: false, howl: null, value: '' },
                { id: 3, src: "/watertoad/.mp3", isActive: false, howl: null, value: '' },
                { id: 4, src: "/watertoad/.mp3", isActive: false, howl: null, value: '' },
            ],
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        openinfo(){
            this.infodialog = true;
        },
        submitResponse(){
          console.log(this.userResponse); // ,     
          this.userResponse = ''; //  
        },
        //  
        showHint(){
          this.showPopup=true;
        },
        //  
        closePopup(){
          this.showPopup=false;
        },
        submitSoundRes(){
            this.stopAllSounds();
            if(this.userResult.trim() == ''){
                alert(" !")
            } else {
                if(this.userResult === ''){
                    //   
                    this.dialog = true;
                } else {
                    //  
                    this.dialog = true;
                }
            }
        },
        stopAllSounds() {
            this.sounds.forEach((sound) => {
                if (sound.playing) {
                    sound.howl.stop();
                    sound.playing = false;
                    sound.isActive = false;
                }
            });
        },
        handleDialogConfirmation(correct){
            this.dialog = false;
            if(correct){
                if (this.result5 === false){
                    //result5 false  'answerCorrect'  (    )
                    this.$emit('answerCorrect');
                }
                this.userResult = ''; //   
                this.closeP();        //  
            }
        },
        toggleAudio(sound) {
            this.stopAllSoundsExcept(sound.id);

            if (sound.playing) {
                sound.howl.stop();
            } else {
                sound.howl = new Howl({
                src: [sound.src],
                volume: 1,
                onend: () => {
                    sound.playing = false;
                },
                });
                sound.howl.play();
                sound.playing = true;
            }

            //    
            sound.isActive = !sound.isActive;
        },
        stopAllSoundsExcept(exceptId) {
            this.sounds.forEach((sound) => {
                if (sound.playing && sound.id !== exceptId) {
                sound.howl.stop();
                sound.playing = false;
                sound.isActive = false; //     isActive false 
                }
            });
        },

    }
}
</script>

<style scoped>
    img{
        width: 100%;
        border-radius: 8px;
    }
    body{
        margin: 0;
    }
    div{
        box-sizing: border-box;
    }
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
    }
      .white-bg{
        width: 90%; height: 90%;
        position: fixed; 
        /* background-color: white; */
        overflow:hidden;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

      }
    .background{
        width: 100%;
        height: 100%;
    }
    .content-container{
        width:100%;
        position: absolute;
        bottom: 7%;
        max-height: 48%;
        overflow-y: auto;
        overflow-x: hidden;
    }
    .close{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 3%;
        right: 5%;
    
    }
    .sound {
        white-space: nowrap; /*    */
    }
    .sound input[type="radio"],
    .sound audio {
        display:inline-block;
        vertical-align: middle;
        padding-left: 15px;
        padding-right: 0px;
        margin-right: 0;
        /* width: 250px; */
    }
    .custom-submit-button {
        color: white !important; /*    */
        font-weight: bold; /*    */
        font-size: 18px; /*   18px  */
    }
    .card {
    justify-content: center; /*     */
    }
    .ok-btn{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
    }
    div.row.sound-item{
    justify-content: space-evenly;
    }
    div.col {
        flex-basis: 0;
    }
    div.v-input--selection-controls__input{
        margin-right: 16px;
        margin-left: 14px;
    }
    .info{
        position:absolute;
        top: 3%;
        left: 5%;
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
    }
    .infodialog{
        position: absolute;
        box-shadow: none;
        /* width: 30%;
        height: auto; */
    }
    .closeinfo{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 30%;
        right: 5%;
    }
    .pic{
        width: 100%;
        height: auto;
    }
    .title{
        background-color: white;
        position: relative;
    }
    .v-application .info {
        background-color: transparent !important;
        border-color: transparent !important;
    }
        ::v-deep .v-dialog { /*   */
        box-shadow: none !important;
    }
</style>