<script >
export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result7: {
            type: Boolean,
        }
    },
    data(){
      return {
          userResult: '', //   
          showPopup: false, // 
          dialog: false,
          infodialog: false,
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        openinfo(){
            this.infodialog = true;
        },
        submitResponse(){
          console.log(this.userResponse); // ,     
          this.userResponse = ''; //  
        },
        submitSoundRes(){
            if(this.userResult.trim() == ''){
                alert(" !")
            } else {
                this.dialog = true;
            }
        },
        handleDialogConfirmation(correct){
            this.dialog = false;
            if(correct){
                if (this.result7 === false){
                    //result7 false  'answerCorrect'  (    )
                    this.$emit('answerCorrect');
                }
                this.userResult = ''; //   
                this.closeP();        //  
            }
        }

    }
}
</script>

