<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <img class = 'background' src= '@/assets/mission/bg_10.png'>
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <button class="info" @click="openinfo">
                <v-icon size="25">mdi-information-variant-circle</v-icon>
            </button>
            <div class="content">
                <div class="cam-container">
                    <div @click="triggerCamera" class="cam-button mb-3">
                        <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200"> </v-btn>
                    </div>
                    <input type="file" name="photofile" id="photofile" accept="image/*" capture="camera"
                    @change="onPhotoFileChange">
                </div>
                <br>
                <div class="img-container text-center">
                    <img src="" id="photoimg">
                </div>
                <!-- <div class="button-container">
                    <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200" @click="submitResponse"></v-btn>
                </div> -->
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card class="pa-3">
                <v-card-text>
                <v-row align="center" justify="center">
                    <v-col align="center">
                    <div class="img-container text-center">
                        <img :src="uploadedPhoto" id="dialogPhoto" style="max-height:100%; max-width:100%;">
                    </div>
                    <br>
                    <div class="text-center">
                        <p>    <strong></strong>  .</p>
                    </div>
                    <div>
                        <div class="btn_container">
                            <v-btn @click="downloadImage(uploadedPhoto)" class="ok-btn mt-3 pl-10 pr-10" color="#EF8200"></v-btn>
                        </div>
                        <div class="card">
                            <v-btn @click="handleDialogConfirmation" class="ok-btn mt-3 pl-10 pr-10" color="#EF8200"></v-btn>
                        </div>
                    </div>
                    </v-col>
                </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="infodialog" class="infodialog">
            <v-card-text class="title">
                <h3></h3>
                <img @click="infodialog = false" src="@/assets/mission/close.png" class="closeinfo">
            </v-card-text>
                <img src="@/assets/mission/missionInfo/info8.jpeg" class="pic">
        </v-dialog>

    </div>
</template>

<script >
export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result4: {
            type: Boolean,
        }
    },
    components:{
     
    },
    data(){
      return {
        userResult: '', //   
        dialog: false,
        infodialog: false,

        userResponse:'',
        uploadedPhoto: null, //     

      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        openinfo(){
            this.infodialog = true;
        },
        triggerCamera(){
            const photoFileInput = this.$el.querySelector("#photofile");
            photoFileInput.click();
        },
        onPhotoFileChange(event){
              //    .
            const fileReader = new FileReader();
            fileReader.readAsDataURL(event.target.files[0]);
            fileReader.onload = () => {
                const photoDataUrl = fileReader.result;
                this.uploadedPhoto = photoDataUrl;
                this.$store.commit('addPhoto', photoDataUrl);
            }
            this.dialog = true; // 
        },
        handleDialogConfirmation(){
            this.dialog = false;

            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = ''; //   
            this.closeP();        //  
        },
        goToAiPainter(){
            this.$router.push({name:'AIView'});
            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = '';
            this.closeP();

        },

        downloadImage(uploadedPhoto) {
            if (uploadedPhoto) {
                //    
                const a = document.createElement('a');
                a.href = uploadedPhoto;

                //     (: downloaded-image.jpg)
                const filename = `family_img`;
                a.download = filename;

                //     
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
            } else {
                //      (:    )
                alert('  !');
            }
        },

    }
}
</script>

<style scoped>
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .white-bg{
        width: 90%; height: 90%;
        position: fixed; 
        /* background-color: white; */
        overflow:hidden;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }
    .background{
        width: 100%;
        height: 100%;
    }
    .content{
        width:100%;
        position: absolute;
        bottom: 7%;
        max-height: 22%;
        overflow-y: auto;
    }
    .close img{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 3%;
        right: 5%;
    }
    .img-container{
        justify-content: center;
        display: flex;
        align-items: center;
    }
    #photoimg {
        display: none;
        width: 70%;
        height: auto; /*     */
        margin-top: 20px; /*    */
    }
    #photofile{
        display:None;
        /* background: yellow; */
        /* height: 50%; */
        /* width: auto; */
    }
    #dialogPhoto {
        width: 80%; /*    */
    }
    .cam-container {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px; /*    */
    }
    .button-container {
        width: 100%; /*    */
        padding: 10px 20px; /*    */
        display: flex; /*    */
        justify-content: center; /*   */
        margin-top: auto; /*         */
    }
    .custom-submit-button {
        color: white !important; /*    */
        font-weight: bold; /*    */
        font-size: 18px; /*   18px  */
    }
    .card {
    justify-content: center; /*     */
    }
    .ok-btn{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
        width: 150px;
    }
    .info{
        position:absolute;
        top: 3%;
        left: 5%;
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
    }
    .infodialog{
        position: absolute;
        box-shadow: none;
        /* width: 30%;
        height: auto; */
    }
    .closeinfo{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 30%;
        right: 5%;
    }
    .pic{
        width: 100%;
        height: auto;
    }
    .title{
        background-color: white;
        position: relative;
    }
    .v-application .info {
        background-color: transparent !important;
        border-color: transparent !important;
    }
        ::v-deep .v-dialog { /*   */
        box-shadow: none !important;
    }
</style>