<template>
  <v-carousel hide-delimiters height="auto" 
  :cycle="false"
  :continuous="false"
  @input="handleSliderChange">
    <v-carousel-item v-for="(item, i) in items" 
    :key="i" 
    :src="item.src">
    <v-btn 
    v-show="i === 1"
    @click="startsound"
    :color="buttonPressed ? 'blue' : 'defaultColor'"
    fab
    small
    style="top: 2%; left: 2%;"
    >
    <v-icon>{{ buttonPressed ? 'mdi-volume-high' : 'mdi-volume-off' }}</v-icon> 
    </v-btn>
    <div v-show="i===1" class="source-credit">
      "   KT AI   ."
    </div>
      <v-btn x-small color="teal" dark 
      @click="goToMain" class="skipButton">Skip</v-btn>
      <v-btn x-small color="primary" dark
      class="beforeButton"
      v-if="i==0" @click="$router.push({name:'ChooseCourse'})"></v-btn><!--   -->
    </v-carousel-item>
    <!--     X -->
    <v-carousel-item v-if="!hasNext" :src="require('@/assets/tutorialView/  .png')">
      <div class="goToMainPage" @click="goToMain"></div>
    </v-carousel-item>

  </v-carousel>
  
</template>

<script>
export default {
  data() {
    return {
      items: [
        { src: require('@/assets/tutorialView/  .jpg')},
        { src: require('@/assets/tutorialView/1.png'),audio: require('@/assets/test1.mp3') },
        { src: require('@/assets/tutorialView/2.jpg')},
        { src: require('@/assets/tutorialView/3.jpg')},
        { src: require('@/assets/tutorialView/4.png') },
      ],
      currentAudio: null, //    
      started: false,
      buttonPressed: false,

    };
  },
  computed: {
    //     
    hasNext() {
      return this.$refs.carousel ? this.$refs.carousel.hasNext() : false;
    },
  },
  methods:{
    playAudio(audioSrc) {
      if (this.currentAudio) {
        this.currentAudio.pause(); //   
      }
      this.currentAudio = new Audio(audioSrc);
      this.currentAudio.play();
    },
    goToMain() {
      if (this.currentAudio) {
        this.currentAudio.pause(); //  
      }
      this.$router.push({ name: 'MainView' });
    },
    startsound() {
      this.buttonPressed = !this.buttonPressed; //   
      if (this.buttonPressed) {
        //    
        this.playAudio(this.items[1].audio);
      } else {
        //    
        if (this.currentAudio) {
          this.currentAudio.pause();
        }
      }
    },
    handleSliderChange(index){
      //  
      if(index !== undefined && index >= 0){
        this.playAudio(this.items[index].audio);
      }
    }
  }
};
</script>

<style scoped>
.beforeButton{
position: absolute;
top:10px;
left: 10px;
}
.skipButton {
position: absolute;
top: 10px;
right: 10px;
}
.goToMainPage {
position: absolute;
bottom: 15%;
left: 10%;
width: 80%;
height: 10%;
}
.source-credit {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 10px; /*    */
  color: #666; /*   */
  text-align: center;
  padding: 5px;
}
</style>