<template>
  <div class="final-page">
    <button @click="$router.push({name:'MainView'})" class="close ma-2 pa-2">
      <img src="@/assets/mission/close.png">
    </button>

    <div  class="photo">
        <canvas id="canvas" style="display:none;"></canvas>
         <img v-if="mergedImage" :src="mergedImage"/>
       <!-- <img :src="familyphoto" alt="familyphoto"/>  . -->

    </div>
    <div class="present">
      <img :src="require('@/assets/open.png')">
    </div>
    <div class="button-container">
      <v-btn @click="downloadImage" class="ok-btn mt-3 pl-10 pr-10" color="#EF8200"> </v-btn>
    </div>
    <v-dialog v-model="dialog" class="dialog">
            <v-card class="pa-3">
                <v-card-text>
                <v-row align="center" justify="center">
                    <v-col align="center">
                    <br>
                    <div class="text-center">
                        <h3>  <strong> </strong> ? ?</h3>
                        <br>
                        <p>   ,        !</p>
                        <p>    <strong></strong>  </p>
                        <p>      !</p>
                    </div>
                    <div class="final-img" id="dudu">
                      <img :src="require('@/assets/final-dudu.jpg')">
                    </div>
                    <div>
                        <v-btn @click="openGoogleForm" class="google-form-button mt-3 pl-10 pr-10" color="#EF8200">  </v-btn>
                    </div>
                    </v-col>
                </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
  </div>
  
</template>

<script>
import { mapState } from 'vuex';

export default {
name: 'FinalView',
data(){
  return {
    dialog: false,
    mergedImage: null,
  };
},
mounted() { 
  this.combineImage();
 },
computed: {
  ...mapState({familyphoto:"family"})
},
methods: {
  combineImage() {
    const canvas = document.querySelector("#canvas");

    const ctx = canvas.getContext('2d')

    const img2 = new Image();
    img2.src = require('@/assets/final-temp.png');

      //    
    img2.onload = () => {
      // Canvas  
      canvas.width = img2.width;
      canvas.height = img2.height;

      //  
      const img1 = new Image();
      img1.src = this.familyphoto;

      img1.onload = () => {
        //     (  )
        ctx.drawImage(img1, 40, 40, img2.width-83, img2.height-230);
        //     ()
        ctx.drawImage(img2, 0, 0, img2.width, img2.height); 

        // Canvas   
        this.mergedImage = canvas.toDataURL('image/png');
      };

    }
  },
  downloadImage() {
    //      .
    const link = document.createElement('a');
    link.href = this.mergedImage;

    //    .
    link.download = 'familyphoto';

    //    .
    document.body.appendChild(link);
    link.click();

    //   .
    document.body.removeChild(link);

    //     .
    this.dialog = true;
    },

  openGoogleForm(){
    const url = "https://docs.google.com/forms/d/e/1FAIpQLSdBCQgSi7xSaxSddm6OTSFwxXKcOjrNvLxfllJq-o0S7_09OQ/viewform?usp=sf_link";
    const options = "width = 700, height=600, left=200, top=200";
    window.open(url, "_blank", options);
    },
},
}

</script>

<style scoped>
.final-page{
  background-image: url('@/assets/background.png');
  background-size: cover; /*  div    */
  height: 100vh; /*     */
  display: flex;
  justify-content: center; /*    */
  align-items: center; /*    */
  position: relative;
}
.close img{
  width: 22px;
  height: 22px;
  cursor: pointer;
  position: absolute;
  top: 3%;
  right: 5%;
}

.photo {
  position: absolute;
  width: 75%;
  max-width: 80%;
  max-height: auto;
  height: auto;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
}
#canvas{
  max-width: 100%;
  max-height: 100%;
}
.photo img{
  width: 100%;

}

.present{
  position: absolute;
  bottom: 8%;
  width: 50%;
  height: auto;
}
.present img{
  width: 100%;
  height: 100%;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
}
.button-container{
  position: absolute;
  bottom: 5%;
  display: flex; /* Flexbox   */
  flex-direction: column; /*    */
  align-items: center; /*    */
}

.ok-btn{
  color: white !important;
  font-weight: bold;
  font-size: 18px;
  width: 100%;
}
.google-form-button{
  color: white !important;
  font-weight: bold;
  font-size: 18px;
  width: 100%;
}
.dialog{
  width: 90%;
}
.final-img {
  width: 45%;
  height: auto;
  bottom: 5%;

}
.final-img img{
  width: 100%;
  height: 100%;
}
.original_img {
    max-height: 95%;
    max-width: 95%;
}
</style>