<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <img class = 'background' src= '@/assets/mission/bg_4.png'>
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <button class="info" @click="openinfo">
                <v-icon size="25">mdi-information-variant-circle</v-icon>
            </button>
            <div class="pic">
                <img src="@/assets/photo-bandal.png">
            </div>
            <div class="content">
                <p class="notion">   !</p>
                <div class="cam-container">
                    <div @click="triggerCamera" class="cam-button mb-3">
                        <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200"> </v-btn>
                    </div>
                    <input type="file" name="photofile" id="photofile" accept="image/*" capture="camera"
                    @change="onPhotoFileChange">
                </div>
                <br>
                <div class="img-container text-center">
                    <img src="" id="photoimg">
                </div>
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card class="pa-3">
                <v-card-text>
                <v-row align="center" justify="center">
                    <v-col align="center">
                    <div class="img-container text-center">
                        <img :src="uploadedPhoto" id="dialogPhoto" style="max-height:100%; max-width:100%;">
                    </div>
                    <br>
                    <div class="text-center">
                        <p>  </p>
                        <p> <strong></strong>  .</p>
                    </div>
                    <div class="button-container">
                        <div class="btn">
                            <v-btn @click="downloadImage" class="download mr-2" color="#EF8200"></v-btn>
                        </div>
                        <div class="next">
                            <v-btn @click="handleDialogConfirmation" class="nextmission" color="#EF8200">  >></v-btn>
                        </div>
                    </div>
                    </v-col>
                </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="infodialog" class="infodialog">
            <v-card-text class="title">
                <h3> </h3>
                <img @click="infodialog = false" src="@/assets/mission/close.png" class="closeinfo">
            </v-card-text>
                <img src="@/assets/mission/missionInfo/info9.jpeg" class="pic-info">
        </v-dialog>

    </div>
</template>

<script >
import axios from 'axios';
export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result4: {
            type: Boolean,
        }
    },
    data(){
      return {
        userResult: '', //   
        dialog: false,
        infodialog: false,
        error: false,
        errorMessage: '',
        userResponse:'',
        uploadedPhoto: null, //     
        response: null,
        uploadimg: null // 
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        openinfo(){
            this.infodialog = true;
        },
        triggerCamera(){
            const photoFileInput = this.$el.querySelector("#photofile");
            photoFileInput.click();
        },
        onPhotoFileChange(event){
            this.uploadimg = event.target.files[0]
            //    .
            const fileReader = new FileReader();
            fileReader.readAsDataURL(event.target.files[0]);
            fileReader.onload = () => {
                
                const photoDataUrl = fileReader.result;
                this.uploadedPhoto = photoDataUrl;

                //   
                this.$store.commit('addPhoto', photoDataUrl);
                this.convert_img();
            }
            this.dialog = true;
        },
        async convert_img(){

              //     
                const formData = new FormData();
                formData.append("file",this.uploadimg);
                formData.append("convertoption",4);

                this.error = false;
                this.errorMessage = '';

                try {
                    this.response = await axios.post('https://1090-119-194-128-7.ngrok-free.app/aipainter', formData,{responseType: 'blob'});
                    //this.response = await axios.post('http://localhost:8000/aipainter', formData,{responseType: 'blob'});
                    this.imageUrl = URL.createObjectURL(this.response.data);
                } catch (error) {
                    console.error("Error during image processing:", error);
                    this.error = true;
                    this.errorMessage = 'Error processing image. Please try again.';
                }
                this.$store.commit('setFamily', this.imageUrl);
        },

        handleDialogConfirmation(){
            this.dialog = false;

            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = ''; //   
            this.closeP();        //  
        },

        downloadImage() {
            if (this.uploadedPhoto) {
            //    
            const link = document.createElement('a');
            link.href = this.uploadedPhoto;

            //     (: downloaded-image.jpg)
            const filename = 'family_img.jpg';
            link.download = filename;

            //     
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            } else {
            //      (:    )
            alert('  !');
            }
        },

    }
}
</script>

<style scoped>
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
      }

    .img-container{
        justify-content: center;
        display: flex;
        align-items: center;
    }
    #photoimg {
        display: none;
        width: 50%;
        height: auto; /*     */
        margin-top: 20px; /*    */
    }

    #photofile{
        display:None;
    }

    #dialogPhoto {
        width: 80%; /*    */
    }
    .cam-container {
        display: flex;
        justify-content: center;
        align-items: center;
        /* margin-top: 10px;  */
    }
    .button-container {
        width: 100%; /*    */
        padding: 10px 20px; /*    */
        display: flex; /*    */
        justify-content: center; /*   */
        margin-top: auto; /*         */
    }
      .white-bg{
        width: 90%; height: 90%;
        position: fixed; 
        /* background-color: white; */
        overflow:hidden;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

      }
    .background{
        width: 100%;
        height: 100%;
    }

    .content{
        width:100%;
        position: absolute;
        bottom: 0%;
        max-height: 45%;
        overflow-y: auto;

    }

    .close img{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 3%;
        right: 5%;
    }
    .custom-submit-button {
        color: white !important; /*    */
        font-weight: bold; /*    */
        font-size: 18px; /*   18px  */
    }
    .next {
        justify-content: center; /*     */
        }
    .download{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
        width: 110px;
    }
    .nextmission{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
        width: 190px;
    }
    .pic{
        width: auto;
        height: 35%;
        bottom: 17%;
        position: absolute;
        left: 52%;
        transform: translateX(-50%);
    }
    .pic img{
        width: 100%;
        height: 100%;
    }
    .info{
        position:absolute;
        top: 3%;
        left: 5%;
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
    }
    .infodialog{
        position: absolute;
        box-shadow: none;
        /* width: 30%;
        height: auto; */
    }
    .closeinfo{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 30%;
        right: 5%;
    }
    .pic-info{
        width: 100%;
        height: auto;
    }
    .title{
        background-color: white;
        position: relative;
    }
    .v-application .info {
        background-color: transparent !important;
        border-color: transparent !important;
    }
        ::v-deep .v-dialog { /*   */
        box-shadow: none !important;
    }
    .notion{
        text-align: center;
        font-family: 'knps_bandaliregular' !important;
        font-size: 20px;
    }
</style>