<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <img class = 'background' src= '@/assets/mission/bg_9.png'>
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <!-- <button class="info" @click="openinfo">
                <v-icon size="25">mdi-information-variant-circle</v-icon>
            </button> -->
            <div class="content-container" justify="center" align="center">

                <v-text-field
                    label="0000 ?"
                    single-line
                    outlined
                    class="textarea"
                    v-model="userResult"
                    justify="center"
                    style="width: 80%;"
                    align-items="center"
                ></v-text-field>
                
                
                <br>
                <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200" @click="submitSoundRes"></v-btn>
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card>
                <v-card-text>
                    <v-row align="center" justify="center">
                        <v-col align="center">
                            <div style="height:70%; width:70%; display: flex; align-items: center; justify-items: center;">
                                <v-img v-if="(userResult === '') | (userResult === ' ')" src="@/assets/O.png" style="max-height:100%; max-width:100%;"></v-img>
                                <v-img v-else src="@/assets/X.png" style="max-height:100%; max-width:100%;"></v-img>
                            </div>
                            <br>
                            <div v-if="(userResult === '') | (userResult === ' ')" class="text-center">
                                <h3>!</h3> 
                                <h4>  .</h4>
                            </div>
                            <div v-else class="text-center">
                                <h3>  ?</h3>
                                <br>
                                <p>   0   </p>
                                <p><strong></strong> </p>
                                <br>
                                <p>      </p>
                                <p>  ~ </p>
                            </div>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-card-actions class="card">
                <v-btn @click="handleDialogConfirmation((userResult === '')|(userResult === ' '))" class="ok-btn mt-3 pl-10 pr-10" color="#EF8200"></v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script >
export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result8: {
            type: Boolean,
        }
    },
    data(){
      return {
          userResult: '', //   
          showPopup: false, // 
          dialog: false,
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        submitResponse(){
          console.log(this.userResponse); // ,     
          this.userResponse = ''; //  
        },
        //  
        showHint(){
          this.showPopup=true;
        },
        //  
        closePopup(){
          this.showPopup=false;
        },
        submitSoundRes(){
            if(this.userResult.trim() == ''){
                alert(" !")
            } else {
                if(this.userResult === '' || this.userResult === ' '){
                    //   
                    this.dialog = true;
                } else {
                    //  
                    this.dialog = true;
                }
            }
        },
        handleDialogConfirmation(correct){
            this.dialog = false;
            if(correct){
                if (this.result8 === false){
                    //result8 false  'answerCorrect'  (    )
                    this.$emit('answerCorrect');
                }
                this.userResult = ''; //   
                this.closeP();        //  
            }
        }

    }
}
</script>

